import {holidays, trips, cityColor} from '../db';

export default function createCalendar(year, month, worker) {
    const calendar = document.querySelector('#calendar');

    let mon = month - 1; // Ð¼ÐµÑÑÑÑ Ð² JS Ð¸Ð´ÑÑ Ð¾Ñ 0 Ð´Ð¾ 11, Ð° Ð½Ðµ Ð¾Ñ 1 Ð´Ð¾ 12
    let d = new Date(year, mon);
    let md = new Date(year, mon, 0).getDate();	// ÐºÐ¾Ð»-Ð²Ð¾ Ð´Ð½ÐµÐ¹ Ð² Ð¿ÑÐ¾ÑÐ»Ð¾Ð¼ Ð¼ÐµÑÑÑÐµ

    let table = `<table>
					<tr>
						<th class="nameOfDay">ÐÐ¾Ð½ÐµÐ´ÐµÐ»ÑÐ½Ð¸Ðº</th>
						<th class="nameOfDay">ÐÑÐ¾ÑÐ½Ð¸Ðº</th>
						<th class="nameOfDay">Ð¡ÑÐµÐ´Ð°</th>
						<th class="nameOfDay">Ð§ÐµÑÐ²ÐµÑÐ³</th>
						<th class="nameOfDay">ÐÑÑÐ½Ð¸ÑÐ°</th>
						<th class="nameOfDay">Ð¡ÑÐ±Ð±Ð¾ÑÐ°</th>
						<th class="nameOfDay">ÐÐ¾ÑÐºÑÐµÑÐµÐ½ÑÐµ</th>
					</tr>
					<tr>`;

    // Ð´Ð½Ð¸ Ð¿ÑÐ¾ÑÐ»Ð¾Ð³Ð¾ Ð¼ÐµÑÑÑÐ° Ð´Ð»Ñ Ð¿ÐµÑÐ²Ð¾Ð³Ð¾ ÑÑÐ´Ð°
    // * * * 1  2  3  4
    for (let i = 0; i < getDay(d); i++) {

		let iDay = `${md-getDay(d)+1+i}.${d.getMonth()}.${d.getFullYear()}`;	// Ð¿ÐµÑÐµÐ±Ð¸ÑÐ°ÐµÐ¼ÑÐ¹ Ð´ÐµÐ½Ñ Ð¿ÑÐ¾ÑÐ»Ð¾Ð³Ð¾ Ð¼ÐµÑÑÑÐ°

		if (i == 5 || i == 6) {		// ÑÐ°Ð·Ð¼ÐµÑÐºÐ° Ð²ÑÑÐ¾Ð´Ð½ÑÑ Ð¸Ð»Ð¸ Ð¿ÑÐ°Ð·Ð´Ð½Ð¸ÐºÐ¾Ð²
			table += `<td class="grayDay">
							${md-getDay(d)+1+i}
							${holidays.includes(iDay) ? '<div class="dayOff">ÐÑÐ°Ð·Ð´Ð½Ð¸Ðº</div>' : '<div class="dayOff">ÐÑÑÐ¾Ð´Ð½Ð¾Ð¹</div>'}
					  </td>`;
		} else if (holidays.includes(iDay)) {	// ÑÐ°Ð·Ð¼ÐµÑÐºÐ° Ð¿ÑÐ°Ð·Ð´Ð½Ð¸ÐºÐ¾Ð²
			table += `<td class="grayDay">
						${md-getDay(d)+1+i}
						<div class="dayOff">ÐÑÐ°Ð·Ð´Ð½Ð¸Ðº</div>
					  </td>`;
		} else {
			table += `<td class="grayDay">${md-getDay(d)+1+i}</td>`;
		}
    }

    // <td> ÑÑÐµÐ¹ÐºÐ¸ ÐºÐ°Ð»ÐµÐ½Ð´Ð°ÑÑ Ñ Ð´Ð°ÑÐ°Ð¼Ð¸
    while (d.getMonth() == mon) {

		let iDay = `${d.getDate()}.${d.getMonth()+1}.${d.getFullYear()}`;	// Ð¿ÐµÑÐµÐ±Ð¸ÑÐ°ÐµÐ¼ÑÐ¹ Ð´ÐµÐ½Ñ ÑÐµÐºÑÑÐµÐ³Ð¾ Ð¼ÐµÑÑÑÐ°
		let trip = false;	// ÑÐµÐºÑÑÐ¸Ð¹ Ð´ÐµÐ½Ñ ÑÐ¾Ð²Ð¿Ð°Ð´Ð°ÐµÑ Ñ Ð´Ð½ÐµÐ¼ ÐºÐ¾Ð¼Ð¼Ð°Ð½Ð´Ð¸ÑÐ¾Ð²ÐºÐ¸
		let tableTd = '';	// Ð·Ð½Ð°ÑÐµÐ½Ð¸Ðµ ÑÑÐµÐ¹ÐºÐ¸ ÐºÐ°Ð»ÐµÐ½Ð´Ð°ÑÑ, ÐµÑÐ»Ð¸ Ð² ÑÑÐ¾Ñ Ð´ÐµÐ½Ñ ÐºÐ¾Ð¼Ð¼Ð°Ð½Ð´Ð¸ÑÐ¾Ð²ÐºÐ°

		if (worker) {	// ÐµÑÐ»Ð¸ ÑÐ¾ÑÑÑÐ´Ð½Ð¸Ðº Ð¿ÐµÑÐµÐ´Ð°Ð½
			for (let key in trips[worker]) {
				if (key == iDay) {
					trip = true;
					tableTd += `<td>
									${d.getDate()}
									<div class="dayOff" style="background: ${cityColor[trips[worker][key]]};">${trips[worker][key]}</div>
					  			</td>`;
				}
			}
		}

		if (getDay(d) == 5 || getDay(d) == 6) {		// ÑÐ°Ð·Ð¼ÐµÑÐºÐ° Ð²ÑÑÐ¾Ð´Ð½ÑÑ Ð¸Ð»Ð¸ Ð¿ÑÐ°Ð·Ð´Ð½Ð¸ÐºÐ¾Ð²
			table += `<td>
						${d.getDate()}
						${holidays.includes(iDay) ? '<div class="dayOff">ÐÑÐ°Ð·Ð´Ð½Ð¸Ðº</div>' : '<div class="dayOff">ÐÑÑÐ¾Ð´Ð½Ð¾Ð¹</div>'}
					  </td>`;
		} else if (holidays.includes(iDay)) {		// ÑÐ°Ð·Ð¼ÐµÑÐºÐ° Ð¿ÑÐ°Ð·Ð´Ð½Ð¸ÐºÐ¾Ð²
			table += `<td>
						${d.getDate()}
						<div class="dayOff">ÐÑÐ°Ð·Ð´Ð½Ð¸Ðº</div>
					  </td>`;
		} else if (trip) {		// ÑÐ¾ÑÑÑÐ´Ð½Ð¸Ðº Ð²ÑÐ±ÑÐ°Ð½ Ð¸ ÑÐµÐºÑÑÐ¸Ð¹ Ð´ÐµÐ½Ñ ÑÐ¾Ð²Ð¿Ð°Ð´Ð°ÐµÑ Ñ ÐºÐ¾Ð¼Ð¼Ð°Ð½Ð´Ð¸ÑÐ¾Ð²ÐºÐ¾Ð¹
			table += tableTd;
		} else {
			table += '<td>' + d.getDate() + '</td>';
		}

      if (getDay(d) % 7 == 6) { // Ð²Ñ, Ð¿Ð¾ÑÐ»ÐµÐ´Ð½Ð¸Ð¹ Ð´ÐµÐ½Ñ - Ð¿ÐµÑÐµÐ²Ð¾Ð´ ÑÑÑÐ¾ÐºÐ¸
        table += '</tr><tr>';
      }

      d.setDate(d.getDate() + 1);
    }

    // Ð´Ð¾Ð±Ð¸ÑÑ ÑÐ°Ð±Ð»Ð¸ÑÑ Ð´Ð½ÑÐ¼Ð¸ ÑÐ»ÐµÐ´ÑÑÑÐµÐ³Ð¾ Ð¼ÐµÑÑÑÐ°, ÐµÑÐ»Ð¸ Ð½ÑÐ¶Ð½Ð¾
    // 29 30 31 * * * *
    if (getDay(d) != 0) {
      	for (let i = getDay(d); i < 7; i++) {

			let iDay = `${i-getDay(d)+1}.${d.getMonth()+1}.${d.getFullYear()}`;	// Ð¿ÐµÑÐµÐ±Ð¸ÑÐ°ÐµÐ¼ÑÐ¹ Ð´ÐµÐ½Ñ ÑÐ»ÐµÐ´ÑÑÑÐµÐ³Ð¾ Ð¼ÐµÑÑÑÐ°

			if (i == 5 || i == 6) {		// ÑÐ°Ð·Ð¼ÐµÑÐºÐ° Ð²ÑÑÐ¾Ð´Ð½ÑÑ Ð¸Ð»Ð¸ Ð¿ÑÐ°Ð·Ð´Ð½Ð¸ÐºÐ¾Ð²
				table += `<td class="grayDay">
							${i-getDay(d)+1}
							${holidays.includes(iDay) ? '<div class="dayOff">ÐÑÐ°Ð·Ð´Ð½Ð¸Ðº</div>' : '<div class="dayOff">ÐÑÑÐ¾Ð´Ð½Ð¾Ð¹</div>'}
						</td>`;
			} else if (holidays.includes(iDay)) {	// ÑÐ°Ð·Ð¼ÐµÑÐºÐ° Ð¿ÑÐ°Ð·Ð´Ð½Ð¸ÐºÐ¾Ð²
				table += `<td class="grayDay">
							${i-getDay(d)+1}
							<div class="dayOff">ÐÑÐ°Ð·Ð´Ð½Ð¸Ðº</div>
						  </td>`;
			} else {
				table += `<td class="grayDay">${i-getDay(d)+1}</td>`;
			}
      	}
    }

    // Ð·Ð°ÐºÑÑÑÑ ÑÐ°Ð±Ð»Ð¸ÑÑ
    table += '</tr></table>';

    calendar.innerHTML = table;
}

function getDay(date) { // Ð¿Ð¾Ð»ÑÑÐ¸ÑÑ Ð½Ð¾Ð¼ÐµÑ Ð´Ð½Ñ Ð½ÐµÐ´ÐµÐ»Ð¸, Ð¾Ñ 0 (Ð¿Ð½) Ð´Ð¾ 6 (Ð²Ñ)
	let day = date.getDay();
	if (day == 0) day = 7; // ÑÐ´ÐµÐ»Ð°ÑÑ Ð²Ð¾ÑÐºÑÐµÑÐµÐ½ÑÐµ (0) Ð¿Ð¾ÑÐ»ÐµÐ´Ð½Ð¸Ð¼ Ð´Ð½ÐµÐ¼
	return day - 1;
}