import createCalendar from './createCalendar';

export default function nextPrevMonth(nextSelector, prevSelector, calendarTitle, startMonth) {
    const next = document.querySelector(nextSelector),
          prev = document.querySelector(prevSelector),
          title = document.querySelector(calendarTitle),
          selectCurrent = document.querySelector('.select__current');

    title.setAttribute('data-month', startMonth);

    nameMonth(startMonth);

    function nameMonth(startMonth) {
        switch(startMonth) {
            case 1: title.innerText = 'Ð¯Ð½Ð²Ð°ÑÑ 2021'; break;
            case 2: title.innerText = 'Ð¤ÐµÐ²ÑÐ°Ð»Ñ 2021'; break;
            case 3: title.innerText = 'ÐÐ°ÑÑ 2021'; break;
            case 4: title.innerText = 'ÐÐ¿ÑÐµÐ»Ñ 2021'; break;
            case 5: title.innerText = 'ÐÐ°Ð¹ 2021'; break;
            case 6: title.innerText = 'ÐÑÐ½Ñ 2021'; break;
            case 7: title.innerText = 'ÐÑÐ»Ñ 2021'; break;
            case 8: title.innerText = 'ÐÐ²Ð³ÑÑÑ 2021'; break;
            case 9: title.innerText = 'Ð¡ÐµÐ½ÑÑÐ±ÑÑ 2021'; break;
            case 10: title.innerText = 'ÐÐºÑÑÐ±ÑÑ 2021'; break;
            case 11: title.innerText = 'ÐÐ¾ÑÐ±ÑÑ 2021'; break;
            case 12: title.innerText = 'ÐÐµÐºÐ°Ð±ÑÑ 2021'; break;
        }
        title.setAttribute('data-month', startMonth);
    }

    next.addEventListener('click', () => {
        if (startMonth >= 12) {
            startMonth = 1;
        } else {
            startMonth++;
        }

        nameMonth(startMonth);

        if (selectCurrent.getAttribute('data-value')) {
            createCalendar(2021, startMonth, selectCurrent.getAttribute('data-value'));
        } else {
            createCalendar(2021, startMonth);
        }
    });

    prev.addEventListener('click', () => {
        if (startMonth <= 1) {
            startMonth = 1;
        } else {
            startMonth--;
        }

        nameMonth(startMonth);

        if (selectCurrent.getAttribute('data-value')) {
            createCalendar(2021, startMonth, selectCurrent.getAttribute('data-value'));
        } else {
            createCalendar(2021, startMonth);
        }
    });
}